
          @import "@/assets/themes/AppAddict/variables";
          @import "@/assets/core";
        

@import "@/assets/variables.scss";

.modal {
  &::v-deep {
    .vm--modal {
      max-width: 100%;
      background-color: var(--bg);
      border: 2px solid var(--lighter-primary);
    }

    .vm--overlay {
      background: rgba(0, 0, 0, var(--modal-overlay-opacity));
    }
  }

  &_w-footer {
    .modal__body {
      padding-bottom: 10px;
    }
  }

  &__close-button {
    filter: var(--img-filter-inverted);
    position: absolute;
    top: 5px;
    right: 5px;

    img {
      width: 20px;
    }
  }

  &__body {
    position: relative;
    padding: 15px;
    overflow: auto;
    height: 100%;
  }

  &__footer {
    display: flex;
    padding: 10px 15px 15px;
    border-top: 1px solid var(--tertiary-accent);
  }
}
